(function() {
  'use strict';

  angular
    .module('client')
    .directive('pwCheck', pwCheck);

  /** @ngInject */
  function pwCheck() {
    var directive = {
      require: 'ng-Model',
      restrict: 'AE',
      link: linkFunc
    };

    function linkFunc(scope, elem, attrs, ctrl) {
      console.log(attrs);
      var firstPassword = '#' + attrs.name;
      var confirmPassword = '#' + attrs.pwCheck;
      console.log(elem);
      //鼠标离开触发验证事件
      //有人会问假如我用$(elem).add也是对的，的确，其实elem就是jquery对象，所以两种就对
      //可以通过 elem instanceOf jQuery 看返回结果是否为true来判别
      elem.add(firstPassword).on('mouseout', function() {
        scope.$apply(function() {
          ctrl.$setValidity('pwmatch', $(confirmPassword).val() === $(firstPassword).val());
        });
      });
    }
    return directive;
    /** @ngInject */
  }

})();

(function() {
  'use strict';

  angular
    .module('client')
    .directive('acmeMalarkey', acmeMalarkey);

  /** @ngInject */
  function acmeMalarkey(malarkey) {
    var directive = {
      restrict: 'E',
      scope: {
        extraValues: '='
      },
      template: '&nbsp;',
      link: linkFunc,
      controller: MalarkeyController,
      controllerAs: 'vm'
    };

    return directive;

    function linkFunc(scope, el, attr, vm) {
      var watcher;
      var typist = malarkey(el[0], {
        typeSpeed: 40,
        deleteSpeed: 40,
        pauseDelay: 800,
        loop: true,
        postfix: ' '
      });

      el.addClass('acme-malarkey');

      angular.forEach(scope.extraValues, function(value) {
        typist.type(value).pause().delete();
      });

      watcher = scope.$watch('vm.contributors', function() {
        angular.forEach(vm.contributors, function(contributor) {
          typist.type(contributor.login).pause().delete();
        });
      });

      scope.$on('$destroy', function () {
        watcher();
      });
    }

    /** @ngInject */
    function MalarkeyController($log, githubContributor) {
      var vm = this;

      vm.contributors = [];

      activate();

      function activate() {
        return getContributors().then(function() {
          $log.info('Activated Contributors View');
        });
      }

      function getContributors() {
        return githubContributor.getContributors(10).then(function(data) {
          vm.contributors = data;

          return vm.contributors;
        });
      }
    }

  }

})();

